export default {
    // components: {
    //     vtable,
    //     seleinter,
    //     susstable,
    //     saleshangpu,
    //     spintime: '',
    // },
    data() {
        return {
            activeKey: 1,
            current: 0,
            selectedRowKeys: [], // Check here to configure the default column
            list: [],
            houselist: {},
            bieshu: {},
            array:[],
            QunFalist:[],
        };
    },
    watch:{   //监听路由变化
        $route( to , from ){   
       //  console.log( to , from )
        this.current = 0;
        if(to.path == '/salerelease'&& from.path == '/shangpuluru'){
            this.activeKey = 2
        }
        // if(to.path == '/salerelease'&& from.path == '/zhandian'){
        //     this.current = 0;
        // }
        // if(to.path == '/salerelease'&& from.path == '/sell'){
        //     this.current = 0;
        // }
            // to , from 分别表示从哪跳转到哪，都是一个对象
            // to.path  ( 表示的是要跳转到的路由的地址 eg: /home );
        }
    },
    methods: {
        callback(key) {
           // console.log('点击tab改变'+key);
            this.activeKey = key
        },
        adduser() {
            this.$router.replace('/zhandian')
        },

        pre() {
            if (this.current-- < 1){
                 this.current = 0;
                 this.bieshu = [];
                 this.QunFalist = [];
            }
                this.bieshu = [];
                 this.QunFalist = [];
        },
        //继续发布
        //   goon(){           
        //         this.current = 0;
        //   },
        getcurrentFormSon(data) {
            this.current = data
        },
        //接受house对象 
        getMag(house) {
            // console.log("house:"+JSON.stringify(house))
            if (house != "" && house != undefined) {
            this.current = 1;
            this.bieshu = house;
            }
            
        },
        getMagList(houseLists){
            // console.log("house:"+JSON.stringify(houseLists))
            if (houseLists.length>0) {
                this.current = 1;
                this.QunFalist = houseLists;
            }
        },
        //第二步操作跳转第三步
        getSeconds(current,length) {
            this.current = current
            this.spintime = length
            console.log(current,length)
        },
        // getSpin(spinning){
        //     console.log('spinning状态'+spinning)
        // },
        rest(){
            this.current = 0
            // console.log("切换"+this.current)
        }
    },
};
